import React from "react";
import styled from "styled-components";
import Service from "./Service";
import NoItems from "./ErrorHandling/NoItems";
import ServiceProcedures from "./ServiceProcedures";

const Container = styled.div.attrs({
  className:
    "w-11/12 md:w-4/5 bg-white dark:bg-slate-700 rounded-xl drop-shadow-lg m-auto mb-8 lg:mb-24 min-h-full flex-col pb-4",
})``;
const SectionTitle = styled.h1.attrs({
  className:
    "text-xl sm:text-2xl lg:text-3xl text-white bg-blue-500 text-center w-full h-full rounded-t-xl py-4 md:py-4 font-medium",
})``;
const ServicesContainer = (props) => {
  let Component = "";
  switch (props.id) {
    case "list-layanan":
      Component = props.data ? (
        props.data.map((item) => <Service key={item.id} data={item}></Service>)
      ) : (
        <NoItems></NoItems>
      );
      break;
    case "prosedur-layanan":
      Component = <ServiceProcedures></ServiceProcedures>;
      break;
    default:
      Component = <NoItems></NoItems>;
  }

  return (
    <Container>
      <SectionTitle id={props.id}>{props.title}</SectionTitle>
      {Component}
    </Container>
  );
};

export default ServicesContainer;
