import React from 'react'
import styled from 'styled-components';
import {Email} from '@mui/icons-material';
const Container = styled.footer.attrs({
    className:'w-full bg-blue-600 py-8 px-4 md:px-8 text-white dark:text-gray-200 drop-shadow-xl'
})``;
const Content = styled.div.attrs({
    className:'flex flex-col md:flex-row'
})``;
const Left = styled.div.attrs({
    className:'w-full bg-blue-600 p-4 md:p-8 flex justify-center justify-start'
})``;
const ContentLists = styled.ul.attrs({
    className:''
})``;
const ContentItem = styled.li.attrs({
    className:'flex mt-2'
})``;
const ContentItemTitle=styled.h1.attrs({
    className:'ml-4'
})``;
const Right = styled.div.attrs({
    className:'w-full bg-blue-600 p-4 md:p-8 text-center md:text-start'
})``;
const Copyright = styled.p.attrs({
    className:'text-center text-medium w-full border-t border-white pt-4'
})``;
const Footer = () => {
  return (
    <Container>
        <Content>
            <Left>
                <ContentLists>
                    <ContentItem>
                        <Email></Email>
                        <ContentItemTitle><a href="mailto:kemahasiswaan@stis.ac.id">kemahasiswaan@stis.ac.id</a></ContentItemTitle>
                    </ContentItem>
                    <ContentItem>
                        <Email></Email>
                        <ContentItemTitle><a href="mailto:baak@stis.ac.id">baak@stis.ac.id</a></ContentItemTitle>
                    </ContentItem>
                </ContentLists>
            </Left>
            <Right>
                <h1 className='font-medium text-xl'>Bagian Administrasi Akademik dan Kemahasiswaan</h1>
                <h2 className='mt-4 font-medium text-lg'>Politeknik Statistika STIS</h2>
                <p className='mt-4'>Jl. Otto Iskandardinata No.64C, RT.1/RW.4, Bidara Cina, Kecamatan Jatinegara, Kota Jakarta Timur, Daerah Khusus Ibukota Jakarta 13330</p>
            </Right>
        </Content>
        <Copyright>Copyright  Politeknik Statistika STIS. All right reserved.</Copyright>
    </Container>
  )
}

export default Footer