import React, { useState} from 'react';
// import { XIcon } from '@heroicons/react/outline'
import styled from 'styled-components';
import {Person,Close} from '@mui/icons-material';
import Sidebar from './Sidebar';
import { Link } from "react-router-dom";
import Notification from './Notification';
const Container = styled.nav.attrs({
    className:'w-full drop-shadow-lg py-1 justify-between sm:p-2 pt-2 fixed top-0 left-0 right-0 z-50 bg-white flex dark:bg-slate-800',
})``;
const LeftSide = styled.div.attrs({
    className:'flex items-center'
})``;
const LogoLeft = styled.div.attrs({
    className:'flex items-center'
})``;
const LogoRight = styled.div.attrs({
    className:'flex flex-col justify-center mx-4'
})``;
const Logo = styled.img.attrs({
    className:'w-12 h-12 aspect-square ml-2',
})``;
const LogoTitle = styled.h1.attrs({
    className:'sm:mx-0 border p-2 dark:border-blue-400 dark:text-blue-400 sm:border-0 sm:p-0 rounded border-blue-400 text-2xl text-blue-600 font-bold',
})``;
const LogoSubtitle = styled.h2.attrs({
    className:'hidden sm:inline text-lg font-regular text-gray-500 dark:text-gray-200',
})``;
const RightSide = styled.div.attrs({
    className:'flex mx-4 items-center'
})``;
const ProfileContainer = styled.div.attrs({
    className:'flex justify-between items-center ml-4 hover:cursor-pointer',
})``;
// const ProfileDrawerContainer = styled.div.attrs({
//     className:'flex flex-col justify-between items-center mb-4',
// })``;
const ProfileLeft = styled.div.attrs({
    className:'',
})``;
const ProfileRight = styled.div.attrs({
    className:'mx-2 hidden sm:inline',
})``;
// const ProfileRightDrawer = styled.div.attrs({
//     className:'ml-4 mr-2',
// })``;
const Username = styled.div.attrs({
    className:'font-medium text-gray-700 dark:text-gray-200',
})``;
const NIM = styled.div.attrs({
    className:'text-gray-500 dark:text-blue-300',
})``;
const Drawer = styled.aside.attrs({
    id:'drawer',
    className:'max-w-[300px] w-3/5 drop-shadow-xl dark:bg-slate-800 h-screen bottom-0 top-0 bg-white z-20 absolute transition ease-in-out p-4 right-0 duration-300 translate-x-full',
})``;
// const Drawer = styled.aside.attrs({
//     id:'drawer',
//     className:'max-w-[300px] w-3/5 drop-shadow-xl dark:bg-slate-800 h-screen bottom-0 top-0 bg-white z-20 absolute transition ease-in-out p-4 right-0 duration-300 translate-x-full',
// })``;
// const DrawerContent = styled.ul.attrs({
//     className:'text-gray-600'
// })``;
// const DrawerItem = styled.li.attrs({
//     className:'mt-4 dark:text-gray-300 ml-4 hover:cursor-pointer hover:text-blue-500 dark:hover:text-blue-500 text-lg'
// })``;
// const NavNotificationList = styled.div.attrs({
//     className:'h-fit py-1 w-72 md:w-96 flex flex-col'
// })``;
const MemoizedNotificationComponent = React.memo(Notification);
const Navbar = () => {
    const [isDrawerActive, setDrawerActive] = useState(false);
    //filter notifikasi yang visible
    //ngambil dari localstorage, kalau ngga ada, setting false, kalau ada setting nilai dari localstorage
    // let isDarkTheme = localStorage.getItem('isDarkTheme') ? Boolean(parseInt(localStorage.getItem('isDarkTheme'))):false;
    // const [checked, setChecked] = useState(isDarkTheme);
    // const MemoizedNotificationComponent = useMemo(Notification);
    // const htmlTag = document.getElementsByTagName('HTML')[0];

    // if(checked){
    //     htmlTag.classList.add('dark');
    // }else{
    //     htmlTag.classList.remove('dark');
    // }
    const onProfileClickHandler = (e)=>{
        e.preventDefault();
        const drawerElement = document.getElementById('drawer');
        if(window.location.pathname!=='/dashboard' || window.innerWidth<1028){
                    
            if(isDrawerActive){
                drawerElement.classList.add('translate-x-full');
            }else{
                drawerElement.classList.remove('translate-x-full');
                
            }
            setDrawerActive(prev=>!isDrawerActive);
            }
    }
    
  return (
    <Container>
        <LeftSide>
            <LogoLeft>
                <Logo src='../assets/logo.png' alt='logo polstat'></Logo>
            </LogoLeft>
            <Drawer>
                <Close className='active text-gray-400 mb-4 hover:cursor-pointer shadow-none hover:text-blue-400 border rounded-lg border-blue-400' sx={{ fontSize: 40 }} onClick={onProfileClickHandler}></Close>
                <br />
                <Sidebar></Sidebar>
            </Drawer>
            
            <LogoRight>
                <Link to='/'>
                    <LogoTitle>SIKA</LogoTitle>
                </Link>
                
                <LogoSubtitle>Layanan Kemahasiswaan BAAK</LogoSubtitle>
            </LogoRight>
        </LeftSide>
        <RightSide>
            <MemoizedNotificationComponent></MemoizedNotificationComponent>
          
            <ProfileContainer onClick={onProfileClickHandler}>
                <ProfileLeft>
                    <Person className='hover:ring hover:ring-white  text-white bg-blue-500 transition ease-in-out duration-300 rounded-full p-1' sx={{ fontSize: 40 }}></Person>
                </ProfileLeft>
                <ProfileRight>
                    <Username>Fikri Septrian Anggara</Username>
                    <NIM>221810306</NIM>
                </ProfileRight>
            </ProfileContainer>
        </RightSide>
    </Container>
  )
}

export default Navbar