import React from 'react'
// import heroBg from '../assets/hero/hero-bg.svg';
// import heroImg from '../assets/hero/heroChar.svg';
// import heroBgSm from '../assets/hero/hero-bg-small.svg';
import styled from 'styled-components';

const Container = styled.header.attrs({
    className:'w-full static lg:relative flex items-center justify-center mb-96 sm:mb-48 lg:mb-[800px]'
})``;
const HeroBgLg = styled.img.attrs({
    className:'hidden md:inline lg:absolute top-0 right-0 w-full z-0',
})``;
const HeroBgSm = styled.img.attrs({
    className:'inline md:hidden lg:absolute w-full z-0',
})``;
const DescContainer = styled.div.attrs({
    className:'absolute z-10 flex flex-col md:flex-row w-[75%] lg:top-[100px] top-[100px] items-start'
})``;
const DescDetailContainer = styled.div.attrs({
    className:'lg:w-6/12 w-full flex flex-col justify-center'
})``;
const DescDetailTitle = styled.h1.attrs({
    className:'text-2xl text-center md:text-left md:text-3xl lg:text-5xl text-white my-4 font-medium dark:text-gray-200'
})``;
const DescDetailSubtitle = styled.h2.attrs({
    className:'text-lg text-center md:text-left md:text-xl lg:text-3xl text-white my-4 font-regular dark:text-gray-200'
})``;
const DescDetailButton = styled.button.attrs({
    className:'text-gray-200 bg-blue-700 md:bg-gray-100 ease-in-out drop-shadow-lg duration-300 md:hover:bg-gray-300 mx-auto md:mx-0 w-full md:text-blue-600 p-4 mt-16 sm:mt-8 md:text-xl text-md font-medium rounded-md md:w-36'
})``;
const DescContainerImg = styled.img.attrs({
    className:'lg:w-6/12 md:mt-48 mt-8 md:max-w-[50%] max-w-[100%] mx-auto'
})``;
const Hero = () => {
  return (
    <Container>
    <DescContainer>
        <DescDetailContainer>
          <DescDetailTitle >SIKA membantu pelayanan kemahasiswaan BAAK</DescDetailTitle>
          <DescDetailSubtitle>Kami membantu menghubungkan anda dengan BAAK untuk mempermudah proses pelayanan kemahasiswaan</DescDetailSubtitle>
        <a href="#list-layanan">
            <DescDetailButton>Mulai</DescDetailButton>
        </a>  
        </DescDetailContainer>
        <DescContainerImg src='../assets/hero/heroChar.svg' alt="Hero" />
    </DescContainer>
    <HeroBgLg src='../assets/hero/hero-bg.svg' alt="gambar" />
    <HeroBgSm src='../assets/hero/hero-bg-small.svg' alt="gambar" />
    </Container>
  )
}

export default Hero