import React from 'react'
import Hero from '../components/Hero';
import Navbar from '../components/Navbar';
import ServicesContainer from '../components/ServicesContainer';
import Footer from '../components/Footer';
import {servicesItems} from '../data';

const Home = () => {
  return (
      <div className="relative w-full bg-gray-100 dark:bg-slate-800">
        <Hero></Hero>
        <Navbar></Navbar>
        <ServicesContainer id='prosedur-layanan' title={'Cara Menggunakan Layanan'}></ServicesContainer>
        <ServicesContainer id='list-layanan' data = {servicesItems} title = {'Layanan yang Tersedia'}></ServicesContainer>
        <Footer></Footer>
      </div>

  )
}

export default Home