import React, {useState} from 'react'
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';
import {Person, ExpandMore, Dashboard, Home, Logout} from '@mui/icons-material';
import styled from 'styled-components';
import { Link } from "react-router-dom";


const ProfileDrawerContainer = styled.div.attrs({
    className:'flex flex-col items-center mb-4 text-left hover:cursor-pointer',
})``;
const ProfileRightDrawer = styled.div.attrs({
    className:'ml-4 mr-2',
})``;

const DrawerContent = styled.ul.attrs({
    className:'text-gray-600'
})``;
const DrawerItem = styled.li.attrs({
    className:'mt-4 dark:text-gray-300 ml-4 hover:cursor-pointer hover:text-blue-500 dark:hover:text-blue-500 text-lg'
})``;
const ProfileLeft = styled.div.attrs({
    className:'',
})``;
// const ProfileRightDrawer = styled.div.attrs({
//     className:'ml-4 mr-2',
// })``;
const Username = styled.div.attrs({
    className:'font-medium text-gray-700 dark:text-gray-200',
})``;
const NIM = styled.div.attrs({
    className:'text-gray-500 dark:text-blue-300',
})``;
const Sidebar = () => {
    let isDarkTheme = localStorage.getItem('isDarkTheme') ? Boolean(parseInt(localStorage.getItem('isDarkTheme'))):false;
    const [checked, setChecked] = useState(isDarkTheme);
    // const [isDrawerActive, setDrawerActive] = useState(false);
    const htmlTag = document.getElementsByTagName('HTML')[0];

    if(checked){
        htmlTag.classList.add('dark');
    }else{
        htmlTag.classList.remove('dark');
    }
    const onChangeSwitchHandler = (e)=>{
        const htmlTag = document.getElementsByTagName('HTML')[0];
        if(checked){
            htmlTag.classList.add('dark');
            localStorage.setItem('isDarkTheme',0)
        }else{
            htmlTag.classList.remove('dark');
            localStorage.setItem('isDarkTheme',1)
        }
        setChecked(prev=>!checked);
    }
  return (
    <>
        <ProfileDrawerContainer>
            <ProfileLeft>
                <Person className='text-white bg-blue-500 rounded-full p-1 mb-4' sx={{ fontSize: 40 }}></Person>
            </ProfileLeft>
            <ProfileRightDrawer>
                <Username>Fikri Septrian Anggara</Username>
                <NIM>221810306</NIM>
            </ProfileRightDrawer>
        </ProfileDrawerContainer>
        
        <DrawerContent>
            <Accordion className='style-none dark:bg-slate-800'>
                <AccordionSummary
                expandIcon={<ExpandMore />}
                ><p className='dark:text-gray-300 text-lg'>Layanan</p></AccordionSummary>
                <AccordionDetails>
                    <ul>
                    <DrawerItem>
                        <Link to='/dashboard'>
                            Surat Keterangan Mahasiswa
                        </Link>
                    </DrawerItem>
                    <DrawerItem>
                        <Link to='/dashboard'>
                            Surat Permintaan Data
                        </Link>
                    </DrawerItem>
                </ul>
                </AccordionDetails>
            </Accordion>
            <Link to='/'>
                <DrawerItem>
                    <Home className='mr-2'></Home>
                    Home
                </DrawerItem>
            </Link>
            <Link to='/dashboard'>
                <DrawerItem>
                    <Dashboard className='mr-2'></Dashboard>
                    Dashboard
                </DrawerItem>
            </Link>
            
            
            <DrawerItem>
                <FormControlLabel control={<Switch checked={checked} onChange={onChangeSwitchHandler}/>} label="Dark Mode" />
            </DrawerItem>
            <Link to='/login'>
                <DrawerItem>
                    <Logout className='mr-2'></Logout>
                    Logout
                </DrawerItem>
            </Link>
        </DrawerContent>            
    </>
  )
}

export default Sidebar