import React from 'react'
import styled from 'styled-components';

const DescDetailTitle = styled.h1.attrs({
    className:'text-2xl text-center md:text-left md:text-3xl lg:text-4xl text-white my-4 font-medium dark:text-gray-200'
})``;
const DescDetailSubtitle = styled.h2.attrs({
    className:'text-lg text-center md:text-left md:text-xl lg:text-2xl text-white my-4 font-regular text-gray-200 dark:text-gray-200'
})``;
const LoginHero = () => {
  return (
    <div className='w-full md:w-1/2 flex-col p-4 m-auto'>
        <h1 className='text-3xl text-white font-bold md:text-6xl my-2 md:my-8 text-center md:text-left'>SIKA</h1>
        <h2 className='text-medium text-white font-semibold md:text-2xl my-2 md:my-8 text-center md:text-left'>(Sistem Informasi Layanan Kemahasiswaan)</h2>
        <DescDetailTitle >SIKA membantu pelayanan kemahasiswaan BAAK</DescDetailTitle>
        <DescDetailSubtitle>Kami membantu menghubungkan anda dengan BAAK untuk mempermudah proses pelayanan kemahasiswaan</DescDetailSubtitle>
    </div>
  )
}

export default LoginHero