import React from "react";

const AddOrderCard = (props) => {
  return (
    <div className="bg-white dark:bg-slate-700 flex w-fit  rounded-xl my-4 mr-2 md:mr-4 shadow-lg">
      <img
        className="basis-1/3  bg-gray-100 w-12 aspect-square m-2 md:m-4 rounded-full ring-2"
        src={props.src}
        alt=""
      />
      <h1 className="basis-1/3 text-lg md:text-3xl text-gray-400 dark:text-gray-400 font-medium p-2 md:p-4 md:pl-0 pl-0">
        {props.layanan}
      </h1>
      <button className="basis-1/3  shadow-lg hover:bg-blue-700 px-3 md:px-4 pb-1 transition duration-200 ease-in-out bg-blue-500 rounded-r-lg text-white font-bold text-3xl">
        +
      </button>
    </div>
  );
};

export default AddOrderCard;
