import React from "react";
import styled from "styled-components";
const Container = styled.div.attrs({
  className:
    "w-11/12 md:w-4/5 bg-white dark:bg-slate-700 rounded-xl drop-shadow-lg m-auto mb-8 lg:mb-24 min-h-full flex-col pb-2",
})``;
const SectionTitle = styled.h1.attrs({
  className:
    "text-xl lg:text-2xl text-white dark:text-gray-200 dark:bg-slate-600 text-center w-full h-full rounded-t-xl py-2 font-medium",
})``;

const CardContainer = (props) => {
  let headerColor = "";
  switch (props.headerColor) {
    case "yellow":
      headerColor = "bg-yellow-400";
      break;
    case "green":
      headerColor = "bg-emerald-500";
      break;
    case "red":
      headerColor = "bg-red-500";
      break;
    case "blue":
      headerColor = "bg-blue-500";
      break;
    default:
      headerColor = "bg-gray-200 text-gray-500";
  }
  return (
    <Container>
      <SectionTitle className={headerColor}>{props.title}</SectionTitle>
      {props.children}
    </Container>
  );
};

export default CardContainer;
