import React, {useState, Fragment, useEffect} from 'react'
import { Menu, Transition } from '@headlessui/react'
import { Notifications} from '@mui/icons-material';
import Badge from '@mui/material/Badge';
import {notificationItems} from '../data';
import styled from 'styled-components';
import Loading from '../components/ErrorHandling/Loading';

const NavNotificationList = styled.div.attrs({
    className:'h-fit py-1 w-72 md:w-96 flex flex-col'
})``;
const Notification = () => {
    // console.log('notification rendering..')
    const [isLoading, setIsLoading] = useState(false);
    const [isEmpty, setIsEmpty] = useState(false);
    const [notificationData, setNotificationData] = useState(null)
    const [isError, setIsError] = useState(false);
    const [badgeCount, setBadgeCount] = useState(0);
    const [isUpdated, setIsUpdated] = useState(false);

    const wait = (timeToDelay) => new Promise((resolve) => setTimeout(resolve, timeToDelay));
    //simulasi fetching data, dilakukan sekali, nanti tambahin useMemo di navbar dan simpan di useContext

    useEffect(()=>{
        let abortController;
        // console.log('notification fetching..')
        const fetchData = async ()=>{
            //controller buat batalin fetching data/unsubscribe buat nanti dah fetching data
            abortController = new AbortController();
            setIsLoading(prev=>true);
            await wait(3000);
            //fetch data di sini
            const realData= notificationItems;
            const data = realData.filter(item=>item.visible===true)
            // end fetch data
            setNotificationData(prev=>data);
            setIsLoading(prev=>false);
            if(data!==null){
                setIsError(prev=>false);
               if(data.length>0){
                setBadgeCount(prev=>data.filter(item=>item.visited===false).length);
                setIsEmpty(prev=>false)
                }  else {setIsEmpty(prev=>true)}
            } else {setIsError(prev=>true)}
        }
        fetchData();
        return(()=>{})
    },[]);

    const needTobeUpdateChecker = (isError, notificationData, isUpdated) =>{
            if(!isError && notificationData!==null && isUpdated){
                return true
            }
        return false
    }
    //handle perubahan pada notificationData, setiap perubahan dikirim ke server.
    useEffect(()=>{
        // console.log('notificationData',notificationData);
        let needTobeUpdate = needTobeUpdateChecker(isError, notificationData, isUpdated);
        if(needTobeUpdate){
            // console.log('notification updated..')
            //post updated data ke server
        }
    }, [notificationData]);

    const updateNotificationDataToVisited = (id)=>{
        let tempData = notificationData.slice();
        const filteredNotification = tempData.filter(item=>item.id==id);
        tempData.forEach(item=>{
            filteredNotification.forEach(itemFiltered=>{
                if(item.id===itemFiltered.id){
                    item.visited=true;
                    setBadgeCount(prev=>notificationData.filter(item=>item.visited===false&&item.visible===true).length);
                }
            })
        })
        setNotificationData(prev=>tempData);
        setIsUpdated(prev=>true);
        }
    const notificationItemClickHandler = (e)=>{
        e.preventDefault();
        updateNotificationDataToVisited(e.target.parentNode.id)
    }

    const eraseNotificationHandler = (e) =>{
        e.preventDefault();
        if(notificationData!=null){
            setNotificationData(prev=>{
                const tempData = prev.slice();
            tempData.map(item =>{
                item.visible=false
            });
            setBadgeCount(prev=>0);
                return(tempData)
            });
            setIsEmpty(prev=>true);
            setIsUpdated(prev=>true);
        } else {setIsError(prev=>true)}
    }
  return (
    <Menu as="div" className="relative inline-block text-left">
                <Menu.Button>
                <Badge badgeContent={badgeCount} color="success" >
                    <Notifications className='hover:cursor-pointer hover:ring hover:ring-white text-white bg-blue-500 rounded-full p-1' sx={{ fontSize: 40 }}></Notifications>
                </Badge>
                </Menu.Button>
            <Transition
                as={Fragment}
                enter="transition ease-out duration-100"
                enterFrom="transform opacity-0 scale-95"
                enterTo="transform opacity-100 scale-100"
                leave="transition ease-in duration-75"
                leaveFrom="transform opacity-100 scale-100"
                leaveTo="transform opacity-0 scale-95"
            >
                <Menu.Items className="transition ease-in-out delay-150 origin-top-right absolute right-0 mt-2 rounded-md rounded-b-lg shadow-lg bg-white ring-1 ring-black ring-opacity-5 focus:outline-none ">
                <NavNotificationList id='nav-notification-list' >
                    <ul className='divide-y-2 divide-white h-48 overflow-y-hidden hover:overflow-y-auto p-0 flex-col justify-items-center'>
                        {isLoading ? <Loading></Loading>:''}
                        {isEmpty ? <h1 className='text-center text-gray-400 mt-20'>Tidak ada notifikasi...</h1>:''}
                        {isError ? <h1 className='text-center text-gray-400 mt-20'>error...</h1>:''}
                        {notificationData 
                        && notificationData.length>0 
                        && notificationData.filter(item=>item.visible===true)
                        .map(item=>{
                            const className = item.visited ? 'bg-white px-4 hover:cursor-pointer' : 'bg-blue-100 px-4 hover:bg-blue-50 hover:cursor-pointer transition ease-in-out duration-200';
                            return(
                                <li className={className} id={item.id} key={item.id} onClick={notificationItemClickHandler}>
                                    <h1 className='text-sm text-gray-600 pt-2'>{item.title}</h1>
                                    <p className='h-8 truncate text-xs'>{item.description}</p>
                                    <h2 className='text-xs text-gray-400 text-right'>{item.created_at.toUTCString()}</h2>
                                </li>
                            )
                        })}
                    </ul>
                    <button disabled={isLoading || isEmpty} className={'hover:cursor-pointer transition ease-in-out duration-200 hover:bg-blue-400 absolute bottom-0 z-10 text-regular text-white p-1 w-full rounded-b-md text-center '
                    +(isLoading || isEmpty ? 'bg-blue-400':'bg-blue-600')} onClick={
                        eraseNotificationHandler
                    }>hapus notifikasi</button>
                </NavNotificationList>
                </Menu.Items>
            </Transition>
            </Menu>
  )
}

export default Notification