import React from 'react'
import styled from 'styled-components';
import {Login, Create, CheckBox, Feed, Download, Upload} from '@mui/icons-material';

const Container = styled.div.attrs({
    className:'m-auto grid grid-cols-1 text-sm md:text-base gap-2 lg:grid-cols-2 m-4 md:p-4 mx-4 bg-white dark:bg-slate-700 text-gray-500 dark:text-gray-300'
})``;
const ItemContainer = styled.div.attrs({
    className:'p-2 md:p-3 min-h-full items-center md:items-start flex border border md:border-2 dark:border-slate-800 rounded-lg'
})``;
const Title = styled.h1.attrs({
    className:'text-lg md:text-xl text-start lg:text-2xl text-blue-500 dark:text-blue-200 font-medium'
})``;
const DescriptionContainer = styled.div.attrs({
    className:'text-start items-center p-2'
})``;
const Description = styled.div.attrs({
    className:'mt-2'
})``;
// const template = styled.div.attrs({
//     className:''
// })``;

const ServiceProcedures = () => {
  return (
    <Container>
          <ItemContainer>
              <Login className='text-blue-400 bold mr-2 md:mr-4'sx={{ fontSize: 50 }}></Login>
              <DescriptionContainer>
                  <Title>Login</Title>
                <Description>login menggunakan akun gmail sebelum menggunakan layanan</Description>
              </DescriptionContainer>
          </ItemContainer>
          <ItemContainer className='lg:mt-4'>
              <Feed className='text-blue-400 bold mr-2 md:mr-4'sx={{ fontSize: 50 }}></Feed>
              <DescriptionContainer>
                  <Title>Pilih Layanan</Title>
                <Description>Pilih layanan yang ingin digunakan, dengan cara klik 'Ajukan Permohonan'</Description>
              </DescriptionContainer>
          </ItemContainer>
          <ItemContainer>
              <Create className='text-blue-400 bold mr-2 md:mr-4'sx={{ fontSize: 50 }}></Create>
              <DescriptionContainer>
                  <Title>Isi Form</Title>
                <Description>Isi form sesuai layanan yang dipilih</Description>
              </DescriptionContainer>
          </ItemContainer>
          <ItemContainer className='lg:mt-4'>
              <CheckBox className='text-blue-400 bold mr-2 md:mr-4'sx={{ fontSize: 50 }}></CheckBox>
              <DescriptionContainer>
                  <Title>Tunggu Permohonan Diproses</Title>
                <Description>Selama permohonan diproses, anda akan diberitahu informasi terkait progres persetujuan permohonan. Anda juga dapat melihat progres pada dashboard</Description>
              </DescriptionContainer>
          </ItemContainer>
          <ItemContainer>
              <Download className='text-blue-400 bold mr-2 md:mr-4'sx={{ fontSize: 50 }}></Download>
              <DescriptionContainer>
                  <Title>Download Surat</Title>
                <Description>Apabila disetujui, anda dapat mendownload surat layanan terkait untuk digunakan</Description>
              </DescriptionContainer>
          </ItemContainer>
          <ItemContainer className='lg:mt-4'>
              <Upload className='text-blue-400 bold mr-2 md:mr-4'sx={{ fontSize: 50 }}></Upload>
              <DescriptionContainer>
                  <Title>Upload Surat</Title>
                  <Description className='text-blue-400'>(untuk SPD tujuan BPS)</Description>
                <Description>Anda harus mengupload surat bukti penyerahan data <strong className='dark:text-gray-300 text-gray-500'>apabila SPD digunakan untuk tujuan BPS</strong></Description>
              </DescriptionContainer>
          </ItemContainer>
    </Container>
  )
}

export default ServiceProcedures