import * as React from "react";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import FormGroup from "@mui/material/FormGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
// import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import moment from "moment";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { DesktopDatePicker } from "@mui/x-date-pickers/DesktopDatePicker";
import TextField from "@mui/material/TextField";

export default function Filter() {
  const [layanan, setLayanan] = React.useState("");
  const [value, setValue] = React.useState(new Date());
  const handleChange = (event) => {
    setLayanan(event.target.value);
  };

  return (
    <div className="p-2 w-full border border-gray-300 dark:border-gray-800 rounded-lg">
      <h1 className="dark:text-gray-300 ml-2 text-gray-600 text-regular md:text-lg mb-2">
        Filter
      </h1>
      <FormGroup>
        <div className="flex-col lg:flex-row w-full">
          <FormControl
            sx={{ m: 1, minWidth: 250 }}
            size="small"
            className="text-gray-300"
          >
            <InputLabel id="demo-select-medium" className="dark:text-gray-300">
              Layanan
            </InputLabel>
            <Select
              labelId="demo-select-medium"
              id="demo-select-medium"
              value={layanan}
              label="Layanan"
              onChange={handleChange}
            >
              <MenuItem value="All" className="text-gray-300">
                <em>Semua</em>
              </MenuItem>
              <MenuItem value={"SKM"}>Surat Keterangan Mahasiswa</MenuItem>
              <MenuItem value={"SPD"} className="text-gray-300">
                Surat Permintaan Data
              </MenuItem>
            </Select>
          </FormControl>
          <FormControl sx={{ m: 1, minWidth: 250 }} size="small">
            <TextField
              id="date"
              label="Tanggal"
              type="date"
              defaultValue={value}
              InputLabelProps={{
                shrink: true,
              }}
            />
          </FormControl>
        </div>

        <FormControlLabel
          control={<Checkbox defaultChecked />}
          className="text-gray-600 dark:text-gray-300"
          label="Semua"
        />
        <FormControlLabel
          control={<Checkbox defaultChecked />}
          className="text-gray-600 dark:text-gray-300"
          label="Mengantri"
        />
        <FormControlLabel
          control={<Checkbox defaultChecked />}
          className="text-gray-600 dark:text-gray-300"
          label="Sedang Diperiksa"
        />
        <FormControlLabel
          control={<Checkbox defaultChecked />}
          className="text-gray-600 dark:text-gray-300"
          label="Disetujui"
        />
        <FormControlLabel
          control={<Checkbox defaultChecked />}
          className="text-gray-600 dark:text-gray-300"
          label="Menunggu upload BAST"
        />
        <FormControlLabel
          control={<Checkbox defaultChecked />}
          className="text-gray-600 dark:text-gray-300"
          label="Dikembalikan"
        />
        <FormControlLabel
          className="text-gray-600 dark:text-gray-300"
          control={<Checkbox defaultChecked />}
          label="Ditolak"
        />
      </FormGroup>
    </div>
  );
}
