import React, { useState, useContext } from "react";
import { LockClosedIcon } from "@heroicons/react/solid";
import { useForm } from "react-hook-form";
import { UserContext } from "../Context/UserContext";
import axios from "axios";

export default function LoginForm() {
  const { register, handleSubmit } = useForm();
  const [isLoading, setIsLoading] = useState(false);
  let { user, setUser } = useContext(UserContext);

  const onSubmit = async (dataForm) => {
    setIsLoading((prev) => true);
    const payload = {
      nim: dataForm["NIM"],
      password: dataForm["password"],
    };
    try {
      const response = await axios.post("http://localhost:8082/login", payload);
      const data = await response.data.data;
      await setUser((prev) => data);
    } catch (e) {
      console.log(e.response);
    }

    setIsLoading((prev) => false);
  };

  return (
    <>
      {/*
        This example requires updating your template:

        ```
        <html class="h-full bg-gray-50">
        <body class="h-full">
        ```
      */}
      <div className="flex items-center justify-center py-8 lg:py-12 px-4 sm:px-6 lg:px-24 bg-white h-full md:h-full lg:shadow-xl rounded-t-xl md:rounded-l-xl md:rounded-tr-none">
        <div className="max-w-md w-full space-y-8">
          <div>
            <img
              src="../assets/logo.png"
              alt="logo polstat"
              className="w-24 aspect-square m-auto"
            />
            <h1 className="text-xl md:text-3xl text-blue-500 text-center font-bold mt-4">
              SIKA
            </h1>
            <h2 className="mt-6 text-center text-lg md:text-xl text-gray-900">
              Sign in dengan akun sipadu anda
            </h2>
          </div>
          <form
            className="space-y-6 w-4/5 m-auto"
            action="#"
            method="POST"
            onSubmit={handleSubmit(onSubmit)}
          >
            <input type="hidden" name="remember" defaultValue="true" />
            <div className="rounded-md shadow-sm -space-y-px">
              <div>
                <label htmlFor="NIM" className="sr-only">
                  NIM
                </label>
                <input
                  id="NIM"
                  name="NIM"
                  type="text"
                  autoComplete="NIM"
                  {...register("NIM")}
                  required
                  className="appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-t-md focus:outline-none focus:ring-blue-500 focus:border-blue-500 focus:z-10 sm:text-sm"
                  placeholder="NIM"
                />
              </div>
              <div>
                <label htmlFor="password" className="sr-only">
                  Password
                </label>
                <input
                  id="password"
                  name="password"
                  type="password"
                  autoComplete="current-password"
                  {...register("password")}
                  required
                  className="appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-b-md focus:outline-none focus:ring-blue-500 focus:border-blue-500 focus:z-10 sm:text-sm"
                  placeholder="Password"
                />
              </div>
            </div>

            {/* <div className="flex items-center justify-between">
              <div className="flex items-center">
                <input
                  id="remember-me"
                  name="remember-me"
                  type="checkbox"
                  className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                />
                <label htmlFor="remember-me" className="ml-2 block text-sm text-gray-900">
                  Remember me
                </label>
              </div>

              <div className="text-sm">
                <a href="/" className="font-medium text-blue-600 hover:text-blue-500">
                  Lupa Password?
                </a>
              </div>
            </div> */}

            <div>
              <button
                type="submit"
                disabled={isLoading ? true : false}
                className={`group relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium ${
                  isLoading ? "bg-blue-400" : "bg-blue-600 hover:bg-blue-700"
                }  rounded-md text-white  focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500`}
              >
                <span className="absolute left-0 inset-y-0 flex items-center pl-3">
                  <LockClosedIcon
                    className="h-5 w-5 text-blue-500 group-hover:text-blue-400"
                    aria-hidden="true"
                  />
                </span>
                Sign in
              </button>
            </div>
          </form>
        </div>
      </div>
    </>
  );
}
