import React from "react";
import styled from "styled-components";
import DashboardSidebar from "./DashboardSidebar";
import CardContainer from "./CardContainer";
import AddOrderCard from "./AddOrderCard";
import NoItems from "./ErrorHandling/NoItems";
import Filter from "./Filter";

const Container = styled.div.attrs({
  className: "flex h-full justify-center lg:justify-end",
})``;
const LeftContainer = styled.div.attrs({
  className: "invisible fixed lg:visible w-1/5 left-0",
})``;
const RightContainer = styled.div.attrs({
  className: "w-full md:w-4/5 flex-col h-fit mt-20 place-self-end",
})``;
// const Header = styled.div.attrs({
//   className:
//     "h-64 sm:h-48 w-full lg:w-4/5 right-0 absolute bg-blue-700 flex md:mt-12 md:pt-12",
// })``;
const MainContent = styled.div.attrs({
  className: "m-4",
})``;
const DashboardContent = () => {
  return (
    <Container>
      <LeftContainer>
        <DashboardSidebar></DashboardSidebar>
      </LeftContainer>
      <RightContainer>
        <div className="w-11/12 md:w-4/5 flex m-auto items-center justify-between sm:justify-start">
          <AddOrderCard layanan="SPD" src="../assets/SPD.svg"></AddOrderCard>
          <AddOrderCard layanan="SKM" src="../assets/SKM.svg"></AddOrderCard>
        </div>
        <CardContainer title="Daftar Permohonan">
          <MainContent>
            <Filter></Filter>
            <NoItems></NoItems>
          </MainContent>
        </CardContainer>
      </RightContainer>
    </Container>
  );
};

export default DashboardContent;
