import React from 'react'
import styled from 'styled-components';

const Container = styled.div.attrs({
    className:'m-auto flex flex-col md:flex-row m-4 border dark:border-slate-800 rounded-lg p-4 mx-4 bg-white dark:bg-slate-700'
})``;
const Left = styled.div.attrs({
    className:'flex-1'
})``;
const Right = styled.div.attrs({
    className:'flex-1 flex flex-col items-center justify-evenly p-4 text-center'
})``;
const ServiceImg = styled.img.attrs({
    className:'aspect-square m-auto rounded-full bg-gray-100 p-2 border w-48 md:w-72'
})``;
const ServiceTitle = styled.h1.attrs({
    className:'text-2xl sm:text-2xl lg:text-4xl text-gray-500 mt-2 dark:text-gray-200 font-medium'
})``;
const ServiceSubtitle = styled.h2.attrs({
    className:'text-lg md:text-2xl text-blue-300 mt-2'
})``;
const ServiceDescription = styled.p.attrs({
    className:'text-regular md:text-lg text-gray-600 mt-2 dark:text-gray-300'
})``;
const ServiceButton = styled.button.attrs({
    className:'text-gray-100 dark:text-gray-100 bg-blue-700 ease-in-out drop-shadow-lg duration-300 md:hover:bg-blue-400 mx-auto md:mx-0 w-full p-4 mt-8 md:text-lg text-md font-medium rounded-md md:w-96 justify-self-end'
})``;
const Service = (props) => {
  return (
    <Container>
        <Left>
            <ServiceImg src={props.data.imageUrl}></ServiceImg>
        </Left>
        <Right>
            <ServiceTitle>{props.data.title}</ServiceTitle>
            <ServiceSubtitle>{props.data.subtitle}</ServiceSubtitle>
            <ServiceDescription>{props.data.description}</ServiceDescription>
            <ServiceButton>Ajukan Permohonan</ServiceButton>
        </Right>
    </Container>
  )
}

export default Service